"use client"

import { useState } from "react"
import Link from "next/link"
import { PlusCircle } from "lucide-react"
import { Button } from "@/components/ui/button"
import { PostsTable } from "@/components/posts-table"
import { PostsCardView } from "@/components/posts-card-view"

// Dummy data for posts
const initialPosts = [
  { id: 1, title: "First Post", status: "Published", date: "2023-05-01" },
  { id: 2, title: "Second Post", status: "Draft", date: "2023-05-02" },
  { id: 3, title: "Third Post", status: "Published", date: "2023-05-03" },
]

export default function PostsPage() {
  const [posts, setPosts] = useState(initialPosts)

  const handleDelete = (id: number) => {
    console.log("Deleting post:", id)
    setPosts(posts.filter((post) => post.id !== id))
  }

  return (
    <div>
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-6 gap-4">
        <h1 className="text-2xl md:text-3xl font-bold">Posts</h1>
        <Button asChild>
          <Link href="/dashboard/posts/new">
            <PlusCircle className="mr-2 h-4 w-4" /> New Post
          </Link>
        </Button>
      </div>
      <div className="hidden md:block">
        <PostsTable posts={posts} onDelete={handleDelete} />
      </div>
      <div className="md:hidden">
        <PostsCardView posts={posts} onDelete={handleDelete} />
      </div>
    </div>
  )
}

